.button {
  width: 100%;
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #122570;
  color: white;
  background: #122570;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.26);
  cursor: pointer;
  border-radius: 5px;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #09143c;
  border-color: #09143c;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.26);
}

@media (min-width: 600px) {
  .button{
    width: auto;
  }
}
